<script>
// 在Vue组件中
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
import crabsFooter from '../../components/pc/footer.vue'
import sliderone from '../../assets/images/swiper1.jpg'
import slidertwo from '../../assets/images/swiper2.jpg'
import sliderthree from '../../assets/images/swiper3.jpg'
import product1 from '../../assets/images/home/product1.png'
import product2 from '../../assets/images/home/product2.png'
import product3 from '../../assets/images/home/product3.png'
import product4 from '../../assets/images/home/product4.png'

import solve1 from '../../assets/images/home/slove-right1.jpg'
import solve2 from '../../assets/images/home/slove-right2.jpg'
import solve3 from '../../assets/images/home/slove-right3.jpg'
import solve4 from '../../assets/images/home/slove-right4.jpg'
import solve5 from '../../assets/images/home/slove-right5.jpg'
import solve6 from '../../assets/images/home/slove-right6.jpg'


export default {
  name: 'Home',

  components: {
    crabsFooter
  },

  data() {
    return {
      slideList: [
        { img: sliderone, title: 'Slide 1' },
        { img: slidertwo, title: 'Slide 2' },
        { img: sliderthree, title: 'Slide 3' }
        // 更多图片...
      ],
      swiperOptions: {
        loop: true, // 循环模式选项
        autoplay: {
          delay: 5000,
          disableOnInteraction: false,
        },
        // 分页器配置
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
        // 导航按钮配置
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
      },

      elementsToAnimate: [
        {
          ref: 'element1',
          triggered: false,
        },
        {
          ref: 'element2',
          triggered: false,
        },
        {
          ref: 'element3',
          triggered: false,
        },
        {
          ref: 'element4',
          triggered: false,
        },
        {
          ref: 'element5',
          triggered: false,
        },
        {
          ref: 'element6',
          triggered: false,
        },
        {
          ref: 'element7',
          triggered: false,
        },
        {
          ref: 'element8',
          triggered: false,
        },
      ],

      productDetails: [
        { image: product1, text: '后勤保障车' },
        { image: product2, text: '后勤保障车' },
        { image: product3, text: '后勤保障车' },
        { image: product4, text: '-后勤保障车-后勤保障车-后勤保障车' },
      ],

      sloveDetails: [
        {
          details: [
            { image: solve1 },
            { text: {
              title: '城市应急',
              word: '城市内涝、火灾、其他安全事故救援、大型会议活动现场饮食保障'
            } },
            { image: solve2 },
          ]
        },
        {
          details: [
            { image: solve3 },
            { image: solve4 },
            { text: {
              title: '抗震救灾',
              word: '地震、洪水、森林火灾等自然灾害救援现场饮食保障'
            } },
          ]
        },
        {
          details: [
            { image: solve5 },
            { image: solve6 },
          ]
        }
      ],
    }
  },

  mounted() {
    window.addEventListener('scroll', this.checkVisibility);
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.checkVisibility);
  },

  methods: {
    checkVisibility() {
      this.elementsToAnimate.forEach(elData => {
        if (!elData.triggered) {
          const el = this.$refs[elData.ref];
          const rect = el.getBoundingClientRect();
          const isInViewport = rect.top >= 0 && rect.bottom <= window.innerHeight;
          if (isInViewport) {
            el.classList.add(el.dataset.animation);
            el.classList.add(el.dataset.ani);
            elData.triggered = true; // 标记为已触发
          }
        }
      });
    },

  }
};
</script>

